body {
  /* background-color: rgb(55, 52, 52); */
  background-image: linear-gradient(rgb(41, 41, 41), #cc0000);
  height: 100%;
  /* float: left; */
  padding: 25px;
}

@keyframes pulse {
  from {
    color: rgba(51, 51, 51, 0.295)
  }
  to {
    color: rgba(221, 221, 221, 0.898)
  }
}

.contact {
  position: relative;
}

.bg-dark {
  background-color: transparent !important;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 10vh;
  background-color: #cc0000;
  background-image: linear-gradient( #cc0000ea, rgba(41, 41, 41, 0.898));
  color: white;
  text-align: center;
}

.PP {
  height: 75vh;
  /* display: inline-block; */
  /* remove extra space below image */
  padding-top: 5%;
  object-fit: contain;
  padding-bottom: 5%;
}

article {
  padding-right: 5%;
}

.carousel-inner {
 
  object-position: center;
}

.center {
  animation: glow 1.5s infinite alternate;
}

center p {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  /* animation: pulse 2s infinite alternate; */
}

@keyframes glow {
  to {
    text-shadow: 0 0 5px #ffcc01;
  }
}

